import { shape, bool, number, string } from 'prop-types';
import React from 'react';

export const Person = ({ person }) => {
  const {
    name,
    age,
    sex,
    isMarried,
    partnerName,
  } = person;

  const partner = sex === 'm'
    ? 'wife'
    : 'husband';

  return (
    <section className="Person">
      <h2 className="Person__name">
        {`My name is ${name}`}
      </h2>
      {age && (
        <p className="Person__age">
          {`I am ${age}`}
        </p>
      )}

      <p className="Person__partner">
        {isMarried
          ? `${partnerName} is my ${partner}`
          : 'I am not married'}
      </p>
    </section>
  );
};

Person.propTypes = {
  person: shape({
    name: string,
    age: number,
    sex: 'm' || 'f',
    isMarried: bool,
    partnerName: string,
  }),
};

Person.defaultProps = {
  person: {
    name: '',
    age: null,
    partnerName: null,
  },
};
